body {
  margin: 0;
}

.wrapper {
  box-sizing: border-box;
  background-color: #00f;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.title {
  color: #fff;
  margin: 20px 0 0;
  font-size: 50px;
  font-style: italic;
}

.hidden {
  opacity: 0;
  transition: opacity .3s;
}

.loader {
  height: 10px;
  margin: 0 0 15px;
}

.task-box {
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 40px;
  display: flex;
}

.task-box__input {
  border: none;
  border-radius: 10px;
  width: 200px;
  height: 30px;
  padding-inline: 15px;
  font-size: 20px;
}

.task-box__input:focus {
  outline: none;
}

.task-box__btn {
  border: none;
  border-radius: 10px;
  height: 32px;
  padding-inline: 15px;
}

.task-box__btn:hover {
  cursor: pointer;
  color: #fff;
  background-color: #9fa2a5;
  scale: .99;
}

.todos {
  flex-direction: column;
  gap: 20px;
  padding-bottom: 40px;
  display: flex;
}

.todoblock {
  position: relative;
}

.todos__item {
  white-space: nowrap;
  text-overflow: ellipsis;
  box-sizing: border-box;
  cursor: default;
  background-color: #fff;
  border-radius: 10px;
  width: 250px;
  max-height: 32px;
  padding-left: 15px;
  padding-right: 40px;
  font-size: 20px;
  line-height: 32px;
  transition: all 1s ease-in-out;
  display: block;
  overflow: hidden;
}

.todos__item.expanded {
  white-space: normal;
  cursor: zoom-out;
  max-height: 500px;
}

.del {
  cursor: pointer;
  background-color: #ff4040b3;
  background-image: url("bin-icon.d0d6e14d.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 25px 25px;
  border: none;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 3px;
  right: 10px;
}

.del:hover {
  scale: .9;
}

@media (width <= 366px) {
  .title {
    text-align: center;
  }

  .task-box {
    justify-content: center;
  }

  .task-box__input, .task-box__btn {
    width: 80%;
  }
}
